import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './pages/Header';
import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Order from './taobao/Order';
import My from './taobao/My';
import More from './taobao/More';
import Detail from './taobao/Detail';
import 'antd-mobile/dist/antd-mobile.css';
import Searchmore from './taobao/Searchmore';

// https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg
const App = ()=>{
	
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/order' component={Order} />
			<Route path='/my' component={My} />
			<Route path='/more' component={More} />
			<Route path='/detail' component={Detail}/>
			<Route path='/searchmore' component={Searchmore}/>
			{/* <Route component={Home} /> */}
			<Route render={()=><Redirect to='/'/>} />
			
		</Switch>
		
	</Router>
	
}
export default App;


