import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
class Tab2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'redTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={<i className='iconfont icon-icon-dianpu'></i>}
                        selectedIcon={<i className='iconfont icon-dianpu'></i>}
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-ceshi'></i>}
                        selectedIcon={<i className='iconfont icon-gouwuche'></i>}
                        title="客服"
                        key="cart"
                    ></TabBar.Item>
                   <TabBar.Item
                        icon={<i className='iconfont icon-shoucang'></i> }
                        selectedIcon={ <i className='iconfont icon-shoucang'></i>}
                        title="收藏"
                        key="shoucang"
                    >
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-gouwuche'></i> }
                        selectedIcon={ <i className='iconfont icon-gouwuche'></i>}
                        title="加入购物车"
                        key="my"
                        style={{backgroundColor:'#ff9500'}}
                    >
			        </TabBar.Item>
                     <TabBar.Item
                        icon={ <i className=''></i>  }
                        selectedIcon={ <i className='iconfont icon-gengduo'></i> }
                        title="立即购买"
                        key="more"
                        style={{backgroundColor:'#ff0036'}}
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

export default withRouter(Tab2);