import React from 'react'
import img from '../assets/banner.jpg'
import 'antd-mobile/dist/antd-mobile.css';
import { SearchBar, WingBlank } from 'antd-mobile';
import './taobaocss/searchbar.css'
import {Carousel} from 'antd-mobile'
import TurnPicture from './TurnPicture'
import Detail from './Detail'
import Product from './Product';
import { NavLink } from 'react-router-dom';
import Mytab from './Mytab'


// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
const Home = (props) => {
    return (
        <div >
            <div id="bar" style={{backgroundColor:'#fa8729'}}>
            <img style={{display:'inline-block',width:'40px'}} src={require('../assets/tao.png')} alt=""/>
           <li style={{listStyle:'none',display:'inline-block'}}><NavLink to='/searchmore'><SearchBar style={{display:'inline-block'}}/> </NavLink></li>
            </div>
            <TurnPicture />
            <Mytab/>
            <img style={{width:'40%',position:'relative',left:'100px',top:'5px'}} src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png'/>
            <Product/>
            {/* <img src={img} alt=""/>
            <img src="https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg" alt=""/> */}
        </div>
    )
}
export default Home
