import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';

class TurnPicture extends React.Component {
  state = {
    data: ['1', '2', '3'],
    
   imgHeight:200,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel className="space-carousel"
          //frameOverflow="visible"
          autoplay
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => this.setState({ slideIndex: index })}
        >
          {/* {this.state.data.map((val, index) => ( */}
            <a
              // key={val}
              href="http://www.alipay.com"
              style={{
                width:'100%',
                display: 'inline-block',
                position: 'relative',
                height: this.state.imgHeight,
                //boxShadow: '2px 1px 1px rgba(0, 0, 0, 0.2)',
              }}
            >
              <img
                //src={`//gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg`}
                src="https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg"
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
            <img  src='https://gw.alicdn.com/imgextra/i3/83/O1CN015qVSgR1CU3VKWHJKR_!!83-0-ppp-picassogw.jpg'/>
            <img  src='https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i1/6000000002911/O1CN01V3cWI11XNHZwxWxn1_!!6000000002911-0-octopus.jpg'/>
          
          {/* ))} */}
        </Carousel>
      </WingBlank>
    );
  }
}

// ReactDOM.render(<TurnPicture />, mountNode);
export default TurnPicture;